@import url(
'https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url(
'https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,400;0,600;0,800;1,300&display=swap');

.dApp {

height: 100vh;
width: 168vh;
display: flex;
flex-direction: column;
align-items: center;


font-family:'Poppins', sans-serif;
text-align: center;
}

h1 {
text-align: center;
font-size: 3em;
font-family: 'Pacifico', cursive;
color:rgb(34, 51, 84);
padding: 1.5em;
}

h2{
font-size: 30px;
text-decoration: underline;
padding-bottom: 20px;
}

h4{
color: rgb(34, 51, 84);
}

input{
width: 400px;
height: 38px;
font-size: 20px;

}

.searchBox > button{
background-color:rgb(34, 51, 84);
height: 38px;
width: 60px;
border: none;
color: white;
box-shadow: 0px 3px 2px rgb(34, 51, 84);
cursor: pointer;
padding: 0;
}

.showResults{
width: 500px;

}

.showResults > h2 > button{
background: none;
border: none;
cursor: pointer;
}
